import makeRequest from '../makeRequest';


export const add = (data) => {
	return makeRequest({
		url: 'https://dev.unicreate.ru:3002' + "/operators",
		method: 'POST',
		data
	})
}

export const remove = (data) => {
	return makeRequest({
		url: 'https://dev.unicreate.ru:3002' + "/operators",
		method: 'DELETE',
		data
	})
}